 .wptravel-block-trip-filters{
    .wp-travel-itinerary-items{
        max-width: 400px;
        #datepicker1,
        #datepicker2{
        padding: 0.6em 2em;
    }
    }    
    .wp-trave-price-range .ui-slider-horizontal .ui-slider-range {
        top: 0;
        height: 100%;
        background: #ff8121;
    }
    .wp-travel-itinerary-items .wp-travel-form-field {
        margin-bottom: 10px;
        label {
            display: block;
            width: 100%;
            font-size: 16px;
            font-weight: 500;
            color: #4e4e4e;
        }
        select, input{
            width: 100%;
            padding: 0.6em 0.6em;
            height: 35px;
        }
        .trip-duration-calender .calender-icon {
            position: absolute;
            top: 39px;
            left: 3px;
            width: 28px;
            line-height: 35px;
            background: none;
        }
    }
    .wp-travel-form-field.wp-travel-trip-duration{
        position: relative;
    }
 }

 .wptravel-block-trip-filters span.trip-duration-calender{
    position:  relative !important;
 }

.wptravel-block-trip-filters .calender-icon::before {
    top: 7px;
    right: -5px;
    position: relative;
}

.wptravel-block-trip-filters .calender-icon:before {
    content: "ÔÅ≥";
    font-family: "Font Awesome 5 Free";
}